import { AuthProvider } from './types';
import { PrivyAuthService } from './privyAuth';
import { Auth0Service } from './auth0Service';
export class AuthService {
    constructor(config) {
        this.authState = {
            status: 'idle',
            session: null
        };
        // Initialize services if configs are provided
        if (config.privyAppId) {
            this.privyService = new PrivyAuthService(config.privyAppId);
        }
        if (config.auth0Domain && config.auth0ClientId) {
            this.auth0Service = new Auth0Service(config.auth0Domain, config.auth0ClientId, config.auth0Audience);
        }
    }
    // Initialize authentication
    async initialize() {
        if (this.privyService) {
            await this.privyService.initialize();
        }
        if (this.auth0Service) {
            await this.auth0Service.initialize();
        }
        // Try to restore session
        await this.restoreSession();
    }
    // Restore session from storage
    async restoreSession() {
        try {
            this.authState.status = 'loading';
            // First check if we have an Auth0 session
            if (this.auth0Service) {
                const session = await this.auth0Service.getSession();
                if (session) {
                    this.authState.session = session;
                    this.authState.status = 'authenticated';
                    return;
                }
            }
            // Then check for Privy session
            if (this.privyService) {
                const session = await this.privyService.getSession();
                if (session) {
                    this.authState.session = session;
                    this.authState.status = 'authenticated';
                    return;
                }
            }
            // No session found
            this.authState.status = 'unauthenticated';
        }
        catch (error) {
            this.authState.status = 'error';
            this.authState.error = error instanceof Error ? error : new Error(String(error));
        }
    }
    // Login with Auth0
    async loginWithAuth0() {
        if (!this.auth0Service) {
            throw new Error('Auth0 service not initialized');
        }
        try {
            this.authState.status = 'loading';
            const session = await this.auth0Service.login();
            if (session) {
                this.authState.session = session;
                this.authState.status = 'authenticated';
            }
            else {
                this.authState.status = 'unauthenticated';
            }
            return session;
        }
        catch (error) {
            this.authState.status = 'error';
            this.authState.error = error instanceof Error ? error : new Error(String(error));
            return null;
        }
    }
    // Handle Privy login response
    async handlePrivyLoginResponse(response) {
        if (!this.privyService) {
            throw new Error('Privy service not initialized');
        }
        try {
            this.authState.status = 'loading';
            const session = await this.privyService.handleLoginResponse(response);
            this.authState.session = session;
            this.authState.status = 'authenticated';
            return session;
        }
        catch (error) {
            this.authState.status = 'error';
            this.authState.error = error instanceof Error ? error : new Error(String(error));
            return null;
        }
    }
    // Logout
    async logout() {
        try {
            this.authState.status = 'loading';
            if (this.authState.session) {
                // Call the appropriate logout method based on the provider
                if (this.authState.session.provider === AuthProvider.AUTH0 && this.auth0Service) {
                    await this.auth0Service.logout();
                }
                else if (this.authState.session.provider === AuthProvider.PRIVY && this.privyService) {
                    await this.privyService.logout();
                }
            }
            // Clear the session
            this.authState.session = null;
            this.authState.status = 'unauthenticated';
        }
        catch (error) {
            this.authState.status = 'error';
            this.authState.error = error instanceof Error ? error : new Error(String(error));
        }
    }
    // Get current auth state
    getAuthState() {
        return this.authState;
    }
    // Check if the user is authenticated
    isAuthenticated() {
        return this.authState.status === 'authenticated' && !!this.authState.session;
    }
    // Get current session
    getSession() {
        return this.authState.session;
    }
}
