import { AuthProvider } from './types';
import { LocalWalletService } from './localWalletService';
// Create a wrapper for Privy authentication service
export class PrivyAuthService {
    constructor(appId, authServerUrl = 'https://api.fly.inc') {
        this.appId = appId;
        this.authServerUrl = authServerUrl;
        this.localWalletService = new LocalWalletService();
    }
    // Get user from Privy auth data
    mapPrivyUserToAuthUser(privyUser) {
        return {
            id: privyUser.id || '',
            email: privyUser.email?.address,
            name: privyUser.name,
            picture: privyUser.avatar,
            twitterHandle: privyUser.twitter?.username,
            walletAddress: privyUser.wallet?.address,
            isAuthenticated: true
        };
    }
    // Initialize Privy auth - called from background script
    async initialize() {
        // Store the app ID in chrome.storage for the popup to use
        await chrome.storage.local.set({ privyAppId: this.appId });
        console.log('PrivyAuthService initialized with appId:', this.appId);
    }
    // Process the login response from Privy and create a local wallet
    async handleLoginResponse(response) {
        console.log('Processing Privy login response:', response);
        // Extract user and token information from Privy response
        const user = this.mapPrivyUserToAuthUser(response.user);
        const token = response.token;
        // Create the auth session
        const session = {
            accessToken: token,
            idToken: user.id,
            provider: AuthProvider.PRIVY,
            token,
            expiresAt: Date.now() + (24 * 60 * 60 * 1000), // 24 hours
            user
        };
        // Store the session in chrome.storage
        await chrome.storage.local.set({ authSession: session });
        console.log('Auth session stored:', session);
        // Automatically create a local wallet for the user if they don't have one
        if (user.id && !user.walletAddress) {
            console.log('No wallet found for user, creating local wallet...');
            const walletAddress = await this.createWallet(user.id);
            if (walletAddress) {
                // Update the session with the new wallet address
                session.user.walletAddress = walletAddress;
                await chrome.storage.local.set({ authSession: session });
                console.log('Auth session updated with local wallet:', walletAddress);
            }
        }
        return session;
    }
    // Log the user out
    async logout() {
        // Clear the auth session from storage
        await chrome.storage.local.remove('authSession');
        // Also clear the local wallet for security
        await this.localWalletService.clearWallet();
        console.log('User logged out, session and wallet cleared');
    }
    // Get the current session from storage
    async getSession() {
        const data = await chrome.storage.local.get(['authSession']);
        if (data.authSession) {
            const session = data.authSession;
            // Check if the session has expired
            if (session.expiresAt && session.expiresAt < Date.now()) {
                await this.logout();
                console.log('Session expired, user logged out');
                return null;
            }
            return session;
        }
        return null;
    }
    // Verify a token with the auth server
    async verifyToken(token, tokenizeTwitterHandle) {
        try {
            console.log('Verifying token with auth server...');
            // Prepare headers for API verification
            const headers = {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`
            };
            // If we're tokenizing a Twitter profile that might be unclaimed,
            // add the Twitter handle to the request headers
            if (tokenizeTwitterHandle) {
                console.log(`Including unclaimed Twitter handle in auth: @${tokenizeTwitterHandle}`);
                headers['X-Tokenize-Twitter-Handle'] = tokenizeTwitterHandle;
            }
            const response = await fetch(`${this.authServerUrl}/api/verify-token`, {
                method: 'POST',
                headers
            });
            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.error || `Auth server error: ${response.status}`);
            }
            const verificationResult = await response.json();
            console.log('Verification result from auth server:', verificationResult);
            if (!verificationResult.success || !verificationResult.claims) {
                throw new Error('Token verification failed on server');
            }
            return verificationResult;
        }
        catch (error) {
            console.error('Token verification error:', error);
            throw error;
        }
    }
    // Get user by Twitter username via the auth server
    async getUserByTwitterUsername(twitterHandle) {
        try {
            console.log(`Looking up user by Twitter handle: @${twitterHandle}`);
            // Since we're using a proxy server for authentication,
            // we need to use the background script to send the message
            return new Promise((resolve, reject) => {
                chrome.runtime.sendMessage({
                    type: 'GET_USER_BY_TWITTER',
                    data: {
                        twitterHandle
                    }
                }, (response) => {
                    if (chrome.runtime.lastError) {
                        console.error('Error sending message to background script:', chrome.runtime.lastError);
                        reject(chrome.runtime.lastError);
                        return;
                    }
                    if (response?.success && response.data) {
                        console.log('Found user by Twitter handle:', response.data);
                        resolve(response.data);
                    }
                    else {
                        console.log('User not found by Twitter handle:', response?.error || 'Unknown error');
                        resolve(null);
                    }
                });
            });
        }
        catch (error) {
            console.error('Error getting user by Twitter handle:', error);
            return null;
        }
    }
    // Process token with unclaimed Twitter handle for tokenization
    async processUnclaimed(token, twitterHandle) {
        try {
            console.log(`Processing unclaimed Twitter handle: @${twitterHandle}`);
            const verificationResult = await this.verifyToken(token, twitterHandle);
            // Check if we got unclaimed Twitter data
            if (verificationResult.unclaimedTwitter) {
                console.log('Unclaimed Twitter data received:', verificationResult.unclaimedTwitter);
                return verificationResult.unclaimedTwitter;
            }
            return null;
        }
        catch (error) {
            console.error('Error processing unclaimed Twitter handle:', error);
            return null;
        }
    }
    // Create a local wallet for a user
    async createWallet(userId) {
        try {
            console.log('Creating local wallet for user:', userId);
            // Check if wallet already exists
            const existingWallet = await this.localWalletService.getWallet();
            if (existingWallet && existingWallet.userId === userId) {
                console.log('Wallet already exists for user:', userId);
                return existingWallet.publicKey;
            }
            // Generate a new wallet
            const walletAddress = await this.localWalletService.generateWallet(userId);
            console.log('Local wallet created successfully:', walletAddress);
            // Update user session with the new wallet address
            const session = await this.getSession();
            if (session) {
                session.user.walletAddress = walletAddress;
                await chrome.storage.local.set({ authSession: session });
            }
            return walletAddress;
        }
        catch (error) {
            console.error('Error creating local wallet:', error);
            return null;
        }
    }
    // Get the user's wallet
    async getWallet() {
        try {
            return await this.localWalletService.getWallet();
        }
        catch (error) {
            console.error('Error getting wallet:', error);
            return null;
        }
    }
    // Send a message to the background script from the popup
    async backgroundMessage(type, data) {
        return new Promise((resolve, reject) => {
            chrome.runtime.sendMessage({ type, data }, (response) => {
                if (chrome.runtime.lastError) {
                    console.error(`Error sending ${type} message:`, chrome.runtime.lastError);
                    reject(chrome.runtime.lastError);
                    return;
                }
                resolve(response);
            });
        });
    }
}
