// Listen for external messages (from web pages)
chrome.runtime.onMessageExternal.addListener((message, sender, sendResponse) => {
    console.log('Received external message:', message, 'from:', sender);
    // Verify the sender is our auth service
    if (sender.origin === 'https://api.fly.inc') {
        if (message.type === 'PRIVY_AUTH_SUCCESS') {
            console.log('Received successful Privy auth data:', message.data);
            // Update auth state
            if (message.data.user) {
                // Set authenticated state
                privyState.authenticated = true;
                privyState.twitterConnected = !!message.data.user.twitterHandle;
                privyState.userInfo = message.data.user;
                // Process the authentication asynchronously
                (async () => {
                    try {
                        // Create a local wallet for the user if they don't have one already
                        if (message.data.user.id && !message.data.user.walletAddress) {
                            console.log('Creating local wallet for user after Privy auth:', message.data.user.id);
                            // Check if wallet already exists
                            const existingWallet = await localWalletService.getWallet();
                            if (existingWallet && existingWallet.userId === message.data.user.id) {
                                console.log('Using existing local wallet for user:', existingWallet.userId);
                                privyState.wallets.connected = true;
                                privyState.wallets.address = existingWallet.publicKey;
                                // Update the user data with the wallet address
                                privyState.userInfo.walletAddress = existingWallet.publicKey;
                            }
                            else {
                                // Generate a new wallet
                                console.log('Generating new wallet for user:', message.data.user.id);
                                const walletAddress = await localWalletService.generateWallet(message.data.user.id);
                                privyState.wallets.connected = true;
                                privyState.wallets.address = walletAddress;
                                // Update the user data with the wallet address
                                privyState.userInfo.walletAddress = walletAddress;
                            }
                        }
                        else if (message.data.user.walletAddress) {
                            // Use the provided wallet address (though we expect this to be null)
                            privyState.wallets.connected = true;
                            privyState.wallets.address = message.data.user.walletAddress;
                        }
                        // Create auth session from privy state
                        const session = {
                            provider: AuthProvider.PRIVY,
                            accessToken: message.data.token,
                            idToken: message.data.user.id,
                            token: message.data.token,
                            expiresAt: message.data.user.expiresAt || (Date.now() + 24 * 60 * 60 * 1000), // 24 hours
                            user: {
                                ...privyState.userInfo,
                                walletAddress: privyState.wallets.address,
                            }
                        };
                        // Store session
                        await chrome.storage.local.set({ authSession: session });
                        // Update auth state
                        authState = {
                            status: 'authenticated',
                            session
                        };
                        console.log('Auth session created and stored:', session);
                        // Forward the message to the popup with updated wallet info
                        chrome.runtime.sendMessage({
                            type: 'PRIVY_AUTH_SUCCESS',
                            data: {
                                ...message.data,
                                user: {
                                    ...message.data.user,
                                    walletAddress: privyState.wallets.address
                                }
                            }
                        });
                    }
                    catch (error) {
                        console.error('Error processing Privy auth:', error);
                    }
                })();
                // Send immediate success response to the auth page
                sendResponse({ success: true });
                // Close the auth tab if we can identify it
                if (sender.tab?.id) {
                    setTimeout(() => {
                        chrome.tabs.remove(sender.tab.id);
                    }, 2000); // Give the user a chance to see the success message
                }
                return true; // Will send response asynchronously
            }
        }
    }
    // Send generic response for other messages
    sendResponse({ success: false, error: 'Unknown message type or sender' });
    return true;
});
// Initialize extension
(async function initialize() {
    try {
        // Check if there's a saved auth session
        const savedSession = await new Promise((resolve) => {
            chrome.storage.local.get('authSession', (result) => {
                resolve(result.authSession);
            });
        });
        // Validate and restore session if it exists and is not expired
        if (savedSession && savedSession.expiresAt > Date.now()) {
            console.log('Restoring valid auth session', savedSession);
            // Set authenticated state
            authState = {
                status: 'authenticated',
                session: savedSession
            };
            // Also restore the Privy state
            privyState.authenticated = true;
            privyState.userInfo = savedSession.user;
            if (savedSession.user.twitterHandle) {
                privyState.twitterConnected = true;
            }
            // Check for stored wallet
            const storedWallet = await localWalletService.getWallet();
            if (storedWallet) {
                console.log('Found stored wallet for user:', storedWallet.userId);
                privyState.wallets.connected = true;
                privyState.wallets.address = storedWallet.publicKey;
            }
            else if (savedSession.user.id) {
                // Create a wallet if needed
                console.log('No stored wallet found, creating one for user:', savedSession.user.id);
                try {
                    const walletAddress = await localWalletService.generateWallet(savedSession.user.id);
                    privyState.wallets.connected = true;
                    privyState.wallets.address = walletAddress;
                    // Update the session with the wallet address
                    if (authState.session) {
                        authState.session.user.walletAddress = walletAddress;
                        await chrome.storage.local.set({ authSession: authState.session });
                    }
                    console.log('Created and stored wallet for user:', savedSession.user.id);
                }
                catch (walletError) {
                    console.error('Error creating wallet during initialization:', walletError);
                }
            }
        }
        else {
            console.log('No valid auth session found');
            authState = {
                status: 'unauthenticated',
                session: null
            };
        }
    }
    catch (error) {
        console.error('Initialization error:', error);
        authState = {
            status: 'unauthenticated',
            session: null
        };
    }
})();
// Add this function to programmatically open the extension popup after token creation
const openExtensionPopup = async (poolId, mintAddress) => {
    try {
        console.log('Opening trading interface for pool:', poolId);
        // First store the navigation data - do this first to ensure it's available
        await chrome.storage.local.set({
            pendingPoolNavigation: {
                poolId,
                mintAddress,
                poolCreated: true,
                timestamp: Date.now()
            }
        });
        // Check if popup is already open
        const popupTabs = await chrome.tabs.query({ url: chrome.runtime.getURL("popup.html") });
        // If popup is open, send message to navigate
        if (popupTabs.length > 0) {
            console.log('Popup already open, sending navigation message');
            popupTabs.forEach(tab => {
                if (tab.id) {
                    chrome.tabs.sendMessage(tab.id, {
                        type: 'NAVIGATE_TO_POOL_DETAILS',
                        data: {
                            poolId,
                            mintAddress,
                            poolCreated: true
                        }
                    });
                }
            });
            return true;
        }
        // Try to open the popup programmatically - this often fails in many browsers due to security restrictions
        try {
            console.log('Attempting to open popup programmatically');
            await chrome.action.openPopup();
            console.log('Popup opened successfully via chrome.action.openPopup()');
            return true;
        }
        catch (popupError) {
            console.error('Error opening popup directly:', popupError);
            // ALWAYS fall back to opening in a new tab as chrome.action.openPopup() is unreliable
            console.log('Falling back to opening in a new tab');
            chrome.tabs.create({
                url: chrome.runtime.getURL("popup.html") + `?poolId=${poolId}&mintAddress=${mintAddress}&direct=true`
            });
            return true;
        }
    }
    catch (error) {
        console.error('Error opening extension popup:', error);
        // Final fallback - always try to open in a new tab if everything else fails
        try {
            console.log('Final fallback: Opening pool details in a new tab');
            chrome.tabs.create({
                url: chrome.runtime.getURL("popup.html") + `?poolId=${poolId}&mintAddress=${mintAddress}&direct=true`
            });
            return true;
        }
        catch (finalError) {
            console.error('All attempts to open popup failed:', finalError);
            return false;
        }
    }
};
