// This file provides a safe window polyfill for service workers
// It ensures 'window' references don't break in service worker contexts

// In a service worker, 'self' is the global object (not window)
const safeWindow = typeof window !== 'undefined' ? window : 
                 typeof self !== 'undefined' ? self : 
                 typeof global !== 'undefined' ? global : {};

// Add a basic process object to avoid errors until the real process polyfill loads
if (!safeWindow.process) {
  safeWindow.process = { 
    env: {},
    browser: true,
    nextTick: function(cb) { setTimeout(cb, 0); }
  };
}

// Expose this safe window globally
if (typeof self !== 'undefined') {
  self.window = safeWindow;
}

// Patch the global scope to handle direct window assignments
if (typeof global !== 'undefined') {
  global.window = safeWindow;
}

// Ensure that any code trying to assign process to window won't fail
// This is specifically addressing the "window.process = process" error
if (typeof process !== 'undefined' && safeWindow.process !== process) {
  Object.defineProperty(safeWindow, 'process', {
    configurable: true,
    enumerable: true,
    get: function() { return process; },
    set: function(value) { /* Allow assignment but don't overwrite getter */ }
  });
}

// Export for module usage
export default safeWindow;
