import { AuthProvider } from './types';
export class Auth0Service {
    constructor(domain, clientId, audience) {
        this.domain = domain;
        this.clientId = clientId;
        this.audience = audience;
    }
    // Map Auth0 user to our AuthUser model
    mapAuth0UserToAuthUser(auth0User) {
        return {
            id: auth0User.sub,
            email: auth0User.email,
            name: auth0User.name,
            picture: auth0User.picture,
            // Extract Twitter handle from user_metadata if available
            twitterHandle: auth0User.user_metadata?.twitter_handle ||
                auth0User.identities?.find((i) => i.provider === 'twitter')?.nickname,
            isAuthenticated: true
        };
    }
    // Initialize Auth0 config in storage
    async initialize() {
        await chrome.storage.local.set({
            auth0Config: {
                domain: this.domain,
                clientId: this.clientId,
                audience: this.audience
            }
        });
    }
    // Launch Auth0 login using chrome.identity API
    async login() {
        // Create the Auth0 login URL with proper parameters
        const redirectURL = chrome.identity.getRedirectURL();
        const authUrl = `https://${this.domain}/authorize?` +
            `client_id=${this.clientId}` +
            `&response_type=token` +
            `&redirect_uri=${encodeURIComponent(redirectURL)}` +
            `&scope=openid profile email` +
            (this.audience ? `&audience=${encodeURIComponent(this.audience)}` : '');
        try {
            // Launch the Auth0 login flow
            const responseUrl = await new Promise((resolve, reject) => {
                chrome.identity.launchWebAuthFlow({ url: authUrl, interactive: true }, (responseUrl) => {
                    if (chrome.runtime.lastError) {
                        reject(chrome.runtime.lastError);
                    }
                    else if (!responseUrl) {
                        reject(new Error('No response URL returned'));
                    }
                    else {
                        resolve(responseUrl);
                    }
                });
            });
            // Parse the response URL to extract token
            const urlParams = new URLSearchParams(responseUrl.split('#')[1]);
            const accessToken = urlParams.get('access_token');
            const expiresIn = urlParams.get('expires_in');
            if (!accessToken) {
                throw new Error('No access token returned');
            }
            // Call Auth0 userinfo endpoint to get the user profile
            const userInfo = await this.getUserInfo(accessToken);
            const user = this.mapAuth0UserToAuthUser(userInfo);
            // Calculate expiration time
            const expiresAt = expiresIn ?
                Date.now() + (parseInt(expiresIn) * 1000) :
                Date.now() + (24 * 60 * 60 * 1000); // Default to 24 hours
            // Create session
            const session = {
                accessToken,
                idToken: userInfo.sub,
                provider: AuthProvider.AUTH0,
                token: accessToken,
                expiresAt,
                user
            };
            // Store the session
            await chrome.storage.local.set({ authSession: session });
            return session;
        }
        catch (error) {
            console.error('Auth0 login error:', error);
            return null;
        }
    }
    // Get user info from Auth0
    async getUserInfo(accessToken) {
        const response = await fetch(`https://${this.domain}/userinfo`, {
            headers: {
                Authorization: `Bearer ${accessToken}`
            }
        });
        if (!response.ok) {
            throw new Error(`Failed to fetch user info: ${response.statusText}`);
        }
        return response.json();
    }
    // Logout
    async logout() {
        await chrome.storage.local.remove('authSession');
    }
    // Get current session
    async getSession() {
        const data = await chrome.storage.local.get(['authSession']);
        if (data.authSession) {
            const session = data.authSession;
            // Check if the session has expired
            if (session.expiresAt && session.expiresAt < Date.now()) {
                await this.logout();
                return null;
            }
            return session;
        }
        return null;
    }
}
