import { Keypair } from '@solana/web3.js';
export class LocalWalletService {
    constructor() {
        this.WALLET_STORAGE_KEY = 'wumbo_local_wallet';
    }
    /**
     * Generate a new Solana wallet and store it in localStorage
     * @param userId User ID to associate with the wallet
     * @returns The public key of the generated wallet
     */
    async generateWallet(userId) {
        try {
            console.log('Generating local wallet for user:', userId);
            // Generate a new Solana keypair
            const keypair = Keypair.generate();
            // Store secret key as array
            const secretKeyArray = Array.from(keypair.secretKey);
            // Store in localStorage with user association
            const walletData = {
                userId,
                publicKey: keypair.publicKey.toString(),
                secretKeyArray,
                createdAt: new Date().toISOString()
            };
            // Store in Chrome storage
            await chrome.storage.local.set({
                [this.WALLET_STORAGE_KEY]: walletData
            });
            console.log('Local wallet generated and stored for user:', userId);
            console.log('Wallet public key:', keypair.publicKey.toString());
            return keypair.publicKey.toString();
        }
        catch (error) {
            console.error('Error generating local wallet:', error);
            throw error;
        }
    }
    /**
     * Get the stored wallet for the current user
     * @returns The wallet data or null if not found
     */
    async getWallet() {
        try {
            const data = await chrome.storage.local.get([this.WALLET_STORAGE_KEY]);
            const walletData = data[this.WALLET_STORAGE_KEY];
            if (walletData) {
                console.log('Retrieved wallet with public key:', walletData.publicKey);
                // Return only non-sensitive data
                return {
                    userId: walletData.userId,
                    publicKey: walletData.publicKey
                };
            }
            console.log('No wallet found in storage');
            return null;
        }
        catch (error) {
            console.error('Error getting local wallet:', error);
            return null;
        }
    }
    /**
     * Check if a wallet exists for the current user
     * @returns True if a wallet exists
     */
    async hasWallet() {
        const wallet = await this.getWallet();
        return wallet !== null;
    }
    /**
     * Delete the stored wallet
     */
    async clearWallet() {
        try {
            await chrome.storage.local.remove([this.WALLET_STORAGE_KEY]);
            console.log('Local wallet cleared');
        }
        catch (error) {
            console.error('Error clearing local wallet:', error);
            throw error;
        }
    }
    /**
     * Get wallet as keypair for signing transactions
     * @returns Solana Keypair or null if not found
     */
    async getWalletKeypair() {
        try {
            const data = await chrome.storage.local.get([this.WALLET_STORAGE_KEY]);
            const walletData = data[this.WALLET_STORAGE_KEY];
            if (!walletData || !walletData.secretKeyArray) {
                console.error('No wallet private key found');
                return null;
            }
            console.log('Creating keypair from stored secret key');
            // Convert array back to Uint8Array
            const secretKey = new Uint8Array(walletData.secretKeyArray);
            // Create keypair from secret key
            const keypair = Keypair.fromSecretKey(secretKey);
            console.log('Keypair created with public key:', keypair.publicKey.toString());
            return keypair;
        }
        catch (error) {
            console.error('Error getting wallet keypair:', error);
            console.error('Error details:', error instanceof Error ? error.message : String(error));
            console.error('Error stack:', error instanceof Error && error.stack ? error.stack : 'No stack trace');
            return null;
        }
    }
}
