// Utility for working with Solana connections in different environments
import { Connection, Commitment } from '@solana/web3.js';

// Custom Connection class that handles service worker limitations
class ServiceWorkerSafeConnection extends Connection {
  constructor(endpoint, commitmentOrConfig) {
    super(endpoint, commitmentOrConfig);
    
    // Override methods to be no-ops in service worker environment
    if (typeof window === 'undefined') {
      // These methods are overridden in service workers
      this.onAccountChange = () => { 
        return 0; 
      };
      
      this.onProgramAccountChange = () => { 
        return 0; 
      };
      
      this.onLogs = () => { 
        return 0; 
      };
      
      this.onSignature = () => {
        return '';
      };
      
      this.onRootChange = () => {
        return 0;
      };
      
      this.onSlotChange = () => {
        return 0;
      };
      
      this.onSlotUpdates = () => {
        return 0;
      };
      
      // Force the use of HTTP for confirmations
      this._confirmTransactionInitialTimeout = () => {
        return this.confirmTransaction = this.confirmTransactionUsingRpcClient;
      };
    }
  }
  
  // Helper method to ensure transactions are always confirmed using HTTP endpoints
  async safeConfirmTransaction(signature, commitment) {
    if (typeof window === 'undefined') {
      // In service worker, use the HTTP-based confirmation
      return this.confirmTransactionUsingRpcClient(signature, commitment);
    } else {
      // In browser, use the standard method
      return super.confirmTransaction(signature, commitment);
    }
  }
  
  // Helper for HTTP-only transaction status checks
  async confirmTransactionUsingRpcClient(signature, commitment) {
    let status = null;
    let timeoutId = 0;
    
    const statusPromise = new Promise((resolve, reject) => {
      const checkStatus = async () => {
        try {
          status = await this.getSignatureStatus(signature, {
            searchTransactionHistory: true,
          });
          
          if (status === null) {
            timeoutId = setTimeout(checkStatus, 1000);
          } else if (status.value && status.value.err) {
            reject(status.value.err);
          } else {
            resolve(status);
          }
        } catch (error) {
          reject(error);
        }
      };
      
      checkStatus();
    });
    
    try {
      const result = await statusPromise;
      return result;
    } catch (error) {
      clearTimeout(timeoutId);
      throw error;
    }
  }
}

// Create a connection appropriate for the current environment
export function createSafeConnection(endpoint, commitmentOrConfig) {
  return new ServiceWorkerSafeConnection(endpoint, commitmentOrConfig);
}

export default createSafeConnection;
