// This file provides polyfills for service worker environments (background scripts)
const Buffer = require('buffer').Buffer;
const process = require('process');

// Ensure window exists even in service workers (as a reference to self)
if (typeof self !== 'undefined' && typeof window === 'undefined') {
  self.window = self;
}

// In service worker, self is the global object
if (typeof self !== 'undefined') {
  self.Buffer = Buffer;
  self.process = process;
}

// Assign to window safely (window will be self in service workers after our polyfill)
if (typeof window !== 'undefined') {
  window.Buffer = Buffer;
  window.process = process;
}

// Also add to globalThis for maximum compatibility
if (typeof globalThis !== 'undefined') {
  globalThis.Buffer = Buffer;
  globalThis.process = process;
}

// Simple WebSocket implementation for service workers
class MockWebSocket {
  constructor(url, protocols) {
    this.url = url;
    this.protocols = protocols;
    this.readyState = 3; // CLOSED
    this.extensions = '';
    this.protocol = '';
    
    // Set default handlers to null
    this.onopen = null;
    this.onclose = null;
    this.onerror = null;
    this.onmessage = null;
    
    // Trigger error in next tick
    setTimeout(() => {
      if (this.onerror) {
        this.onerror({ message: 'WebSockets not available in service workers' });
      }
    }, 0);
  }
  
  send() {
    return false;
  }
  
  close() {
    // Already closed, do nothing
  }
}

// Add WebSocket to service worker environment
if (typeof self !== 'undefined' && typeof self.WebSocket === 'undefined') {
  self.WebSocket = MockWebSocket;
}

// Export for direct imports
module.exports = { Buffer, process, WebSocket: MockWebSocket };
